import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService, TableListService } from '@app/core/http/serviceIndex';
import { FullMockTest } from '@app/shared/models/full-mock-test';
import { MockTestList } from '@app/shared/models/mock-test-list';
import { TableFilter } from '@app/shared/models/table-filter';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-mock-test-list',
  templateUrl: './mock-test-list.component.html',
  styleUrls: ['./mock-test-list.component.css']
})
export class MockTestListComponent implements OnInit {
  tableFilter = <TableFilter>{};

  public mocks: FullMockTest[];

  public searchData: string;
  public isAsc: boolean = true;
  public subscription: Subscription;
  public isLoading: boolean;
  public isAllow: boolean;


  constructor(
    private _cservice: CommonService, private _router: Router,
    private _tableService: TableListService, private _activeRoute: ActivatedRoute
  ) { }



  ngOnInit() {
    this._activeRoute.queryParams.subscribe(
      (params) => {
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
          this.getMocksList(this.tableFilter);
        } else if (params.search) {

          this.tableFilter.search = params.search;
          this.getMocksList(this.tableFilter);
        } else {
          let mock: MockTestList = this._activeRoute.snapshot.data["fullMockTest"];
          this.mocks = mock.questions;
          this.tableFilter = mock.filter;
        }
      }
    )
  }

  getMocksList(currentFilter: TableFilter) {
    this.isLoading = true;
    this.subscription = this._tableService.getMockTestList(currentFilter)
      .subscribe((res: MockTestList) => {
        this.mocks = res.questions;
        this.tableFilter = res.filter;
        this.isLoading = false;
      });
  }





  fitter(sortColumn) {
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.tableFilter.sortColumn = sortColumn;
    this.getMocksList(this.tableFilter);
  }

  public expandId: any;
  expand(mockId) {
    this.expandId = mockId == this.expandId ? undefined : mockId;
  }

  deleteMock(mock) {
    this.subscription = this._tableService.onDeleteMock(mock.questionId)
      .subscribe((res) => {
        this.getMocksList(this.tableFilter);
      })
  }

  goToEvaluate(id) {
    this._router.navigateByUrl("mock-test/evaluate/" + id);
  }

  public isErrText: any;
  public isToast: boolean = false;
  public isErr: boolean = false;
  public isClose: boolean;
  
  shareMock(data) {
    let temp = {
      "email": data.userEmail,
      "mockTestId": data.questionId
    }
    this.subscription = this._tableService.shareMockEvaluate(temp)
      .subscribe((res) => {
        this.isErrText = res.message;
        this.isErr = false;
        this.isClose = false;
      }, (err) => {
        this.isToast = true;
        this.isErrText = err;
        this.isErr = true;
      })
  }

  startClass(data) {
    this.subscription = this._tableService.checkClass(data.data.questionId, data.data.status == "PAUSE" ? "active" : "pause")
      .subscribe((res) => {
        this.getMocksList(this.tableFilter);
      })
  }



  ngOnDestroy(): void {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
