import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DetailService } from '@app/core/http/detail.service';

@Injectable()
export class MockTestDetailResolver implements Resolve<any> {

    constructor(private question: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<any> {

        const mockUrl = `mock-test/${route.params['mockId']}`;

        return this.question.getDetail(mockUrl);
    }
}