import { NgModule } from '@angular/core';
import { Routes, RouterModule, Route } from "@angular/router";
import { FullMockTestResolver } from '@app/modules/manage/mock-test/reslovers/full-mock-test.reslover';
import { MockTestDetailResolver } from '@app/modules/manage/mock-test/reslovers/mock-test-detail.reslover';
import { MockTestCreateComponent } from '@app/modules/manage/mock-test/mock-test-create/mock-test-create.component';
import { MockTestListComponent } from '@app/modules/manage/mock-test/mock-test-list/mock-test-list.component';

const routes: Routes = [
  {
    path: "",
    component:MockTestListComponent,
    resolve: {
      fullMockTest: FullMockTestResolver
    }
  },
  {
    path: "create",
    component:MockTestCreateComponent,
  },
  {
    path: "create/:mockId",
    component:MockTestCreateComponent,
    resolve:{
      mockDetail:MockTestDetailResolver
    }
  }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers:[
    FullMockTestResolver,
    MockTestDetailResolver
  ]
})

export class MockTestRoutingModule { }
